import React from "react";

import { IIndexingDocument } from "../types";

interface IProps {
  readonly body: IIndexingDocument["body"];
}

export const TextBody = ({ body }: IProps) => <div className="body-wrapper">{body}</div>;
TextBody.displayName = "TextBody";



